.social-button {
  width: 34px;
  height: 34px;
  text-align: center;
  background: #fea5bb;
  border-radius: 100%;
  outline: none;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  display: flex;
  position: relative;
}

.social-button:after {
  content: "";
  width: 0;
  height: 0;
  border-radius: 100%;
  transition: all .3s;
  display: block;
  position: absolute;
  top: -1px;
  left: 50%;
}

.social-button:focus, .social-button:hover {
  color: #fff;
}

.social-button:focus:after, .social-button:hover:after {
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  margin-left: calc(-50% - 1px);
}

.social-button i, .social-button svg {
  z-index: 1;
  transition: all .3s;
  position: relative;
}

.social-button i {
  font-size: 13.6px;
}

.social-button svg {
  height: 40%;
  width: 40%;
}

